:root {
  --online-color: #00ffaa;
  --offline-color: #ff0077;
  --gunmetal-gray: #303952;
  --gunmetal-dark: #202633;
  --font-color: #c9c9d4;
  --panel-border: 1px solid var(--online-color);
}

.bot-monitor {
  background-color: var(--gunmetal-gray);
  border-radius: 8px;
  border: var(--panel-border);
  padding: 20px;
  color: var(--font-color);
  font-family: 'Courier New', monospace;
}

h3 {
  color: var(--online-color);
  text-shadow: 0 0 5px var(--online-color);
}

.bot-list {
  list-style: none;
  padding: 0;
}

.bot-item {
  background-color: var(--gunmetal-dark);
  padding: 10px;
  margin-bottom: 8px;
  border-radius: 5px;
  border-left: 5px solid transparent;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bot-item.status-online {
  border-left-color: var(--online-color);
}

.bot-item.status-offline {
  border-left-color: var(--offline-color);
}

.bot-item strong {
  color: #fff;
}

.bot-item span {
  font-size: 0.8em;
  color: var(--font-color);
}
