.reactor-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0d0d0d;
  color: #00ffff;
  padding: 10px;
}

.panels-container {
  display: flex;
  gap: 20px;
  margin-top: 10px;
}

.panel {
  border: 1px solid #00ffff;
  padding: 10px;
  border-radius: 5px;
  width: 150px;
  text-align: center;
}
