:root {
  --gunmetal-gray: #303952;
  --gunmetal-dark: #202633;
  --frame-color: #5a5a70;
  --font-color: #c9c9d4;
  --panel-border: 1px solid var(--frame-color);
}

.browser-panel {
  background-color: var(--gunmetal-dark);
  border-radius: 8px;
  border: var(--panel-border);
  padding: 10px;
  color: var(--font-color);
  font-family: 'Roboto Mono', monospace;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h3 {
  color: #fff;
  text-shadow: 0 0 5px #fff;
}

.browser-frame {
  width: 100%;
  height: 90%;
  background-color: #000;
  border: 2px solid var(--frame-color);
  border-radius: 5px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.browser-frame iframe {
  width: 100%;
  height: 100%;
  border: none;
}

