:root {
  --primary-color: #ff0077; /* Cyberpink */
  --secondary-color: #6a05ad; /* Cyberpurple */
  --background-color: #1a1a2e; /* Deep purple-black */
  --gunmetal-gray: #303952;
  --font-color: #c9c9d4;
  --panel-border: 1px solid var(--primary-color);
}

@keyframes matrix-rain {
  from { transform: translateY(-100%); }
  to { transform: translateY(100%); }
}

.telemetry-panel {
  background-color: rgba(48, 57, 82, 0.7);
  border-radius: 8px;
  border: var(--panel-border);
  padding: 20px;
  color: var(--font-color);
  font-family: 'Courier New', monospace;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.telemetry-panel::before {
  content: "";
  position: absolute;
  top: -100%;
  left: 0;
  width: 100%;
  height: 200%;
  background: linear-gradient(rgba(26, 26, 46, 0.2), var(--primary-color), var(--secondary-color));
  animation: matrix-rain 6s linear infinite;
  opacity: 0.1;
  z-index: -1;
}

h3 {
  color: var(--primary-color);
  text-shadow: 0 0 5px var(--primary-color);
  margin-top: 0;
}

.log-container {
  height: 200px;
  overflow-y: scroll;
  padding-right: 10px;
  scrollbar-width: thin;
  scrollbar-color: var(--primary-color) var(--gunmetal-gray);
}

.log-container::-webkit-scrollbar {
  width: 8px;
}

.log-container::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
  border-radius: 4px;
}

.log-container::-webkit-scrollbar-track {
  background-color: var(--gunmetal-gray);
}

.log-entry {
  white-space: pre-wrap;
  word-wrap: break-word;
  margin-bottom: 8px;
  font-size: 12px;
}

.log-entry span {
  color: var(--secondary-color);
}

