/* Make canvas background full */
#matrix {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

/* Keep hero title high */
.hero {
  text-align: center;
  margin-top: 40px;
}

.ghostship-title {
  font-family: "Brush Script MT", cursive;
  font-size: 4rem;
  color: #a855f7;
  text-shadow: 0 0 25px #c77dff, 0 0 50px #7b2cbf;
  margin: 0;
}

.ghostship-quote {
  font-size: 1.4rem;
  margin: 12px 0 24px;
  color: #e0e0ff;
  font-style: italic;
}

/* Pills */
.pill-choices {
  display: flex;
  gap: 20px;
  justify-content: center;
  margin: 20px 0;
}

.pill {
  font-size: 1.2rem;
  padding: 12px 24px;
  border-radius: 9999px;
  cursor: pointer;
  border: none;
  color: #fff;
  font-weight: bold;
  transition: transform 0.2s;
}

.pill:hover {
  transform: scale(1.08);
}

.pill.blue {
  background: linear-gradient(145deg, #0077ff, #00d4ff);
  box-shadow: 0 0 15px #00d4ff;
}

.pill.red {
  background: linear-gradient(145deg, #ff0033, #ff1a75);
  box-shadow: 0 0 15px #ff0033;
}

/* Video */
.video-section {
  display: flex;
  justify-content: center;
  margin: 30px auto;
  max-width: 800px;
  border: 2px solid #a855f7;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 0 25px #a855f7;
}

.video-section iframe {
  width: 100%;
  height: 400px;
  border: none;
}

/* Big semi-transparent bulletin */
.bulletin {
  margin: 40px auto;
  max-width: 90%;
  min-height: 400px;
  background: rgba(20, 20, 20, 0.3); /* 30% opacity */
  border-radius: 20px;
  padding: 30px;
  box-shadow: 0 0 35px #a855f7;
  text-align: center;
}

.panel-title {
  font-size: 2rem;
  color: #ffb3ff;
  margin-bottom: 20px;
}

.panel-content {
  font-size: 1.5rem;
  line-height: 1.8;
  color: #fff;
}

/* Home Page Styles */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 40px 20px;
  min-height: 100vh;
}

/* Big Hero Title */
.home-title {
  font-size: 3rem;
  color: #c77dff;
  text-shadow: 0 0 14px #ff00ff, 0 0 28px #00ff99;
  margin-bottom: 20px;
  font-family: 'Orbitron', sans-serif;
}

/* Quote Section */
.home-quote {
  font-size: 1.3rem;
  max-width: 900px;
  color: #e0e0e0;
  margin-bottom: 30px;
  line-height: 1.6;
}

/* Video Wrapper */
.home-video {
  width: 100%;
  max-width: 800px;
  margin: 20px auto;
}


/* Bulletin Section */
.home-bulletin {
  margin-top: 40px;
  max-width: 900px;
  text-align: left;
  padding: 20px;
  border-left: 4px solid #c77dff;
  background: rgba(199, 125, 255, 0.08);
  border-radius: 10px;
}

.home-bulletin h2 {
  font-size: 1.8rem;
  color: #c77dff;
  margin-bottom: 12px;
}

.home-bulletin p {
  font-size: 1.1rem;
  color: #ddd;
  line-height: 1.7;
}




