/* ============================= */
/* Global Reset + Fonts */
/* ============================= */
@import url('https://fonts.googleapis.com/css2?family=Orbitron:wght@600&family=Pacifico&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html, #root {
  height: 100%;
  width: 100%;
  background: black;
  font-family: 'Orbitron', sans-serif;
  color: #00ffcc;
  overflow-x: hidden;
}

/* ============================= */
/* Matrix Rain Background Canvas */
/* ============================= */
#matrix,
#matrix-rain {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  background: black;
}

/* ============================= */
/* Title + Header */
/* ============================= */
.topbar {
  text-align: center;
  padding: 20px;
  position: sticky;
  top: 0;
  background: rgba(0,0,0,0.6);
  z-index: 20;
}

.neon-title {
  font-family: 'Pacifico', cursive;
  font-size: 3.5rem;
  color: #ff33cc;
  text-shadow: 0 0 20px #ff33cc, 0 0 40px #ff66ff;
}

.neon-subtitle {
  font-size: 1.2rem;
  color: #00ffcc;
  opacity: 0.8;
}

/* ============================= */
/* Navigation Bar */
/* ============================= */
.nav {
  margin-top: 15px;
}

.nav a {
  display: inline-block;
  margin: 0 12px;
  padding: 12px 24px;
  border-radius: 50px;
  background: linear-gradient(90deg, #00ffcc, #ff33cc);
  color: black;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.1rem;
  transition: transform 0.2s, box-shadow 0.2s;
}

.nav a:hover {
  transform: scale(1.1);
  box-shadow: 0 0 20px #00ffcc, 0 0 40px #ff33cc;
}

/* ============================= */
/* Pills Choice */
/* ============================= */
.pills {
  margin: 30px auto;
  display: flex;
  justify-content: center;
  gap: 40px;
}

.pill {
  padding: 15px 30px;
  border-radius: 50px;
  font-size: 1.4rem;
  font-weight: bold;
  cursor: pointer;
  border: none;
  transition: all 0.3s;
}

.pill.blue {
  background: #00ccff;
  color: black;
  box-shadow: 0 0 20px #00ccff;
}

.pill.red {
  background: #ff0033;
  color: black;
  box-shadow: 0 0 20px #ff0033;
}

.pill:hover {
  transform: scale(1.15);
}

/* ============================= */
/* Video + iFrame Styling */
/* ============================= */
video, iframe {
  width: 90%;
  max-width: 1000px;
  height: 500px;
  border-radius: 20px;
  border: 2px solid #00ffcc;
  margin: 20px auto;
  display: block;
  box-shadow: 0 0 30px #00ffcc, 0 0 60px #ff33cc;
}

/* ============================= */
/* Map Container */
/* ============================= */
.leaflet-container {
  width: 90%;
  height: 600px;
  margin: 30px auto;
  border-radius: 20px;
  box-shadow: 0 0 20px #00ffcc, 0 0 40px #ff33cc;
}

.map-layers-btn {
  background: #111;
  color: #00ffcc;
  padding: 10px 20px;
  border-radius: 30px;
  border: 2px solid #00ffcc;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.3s;
}

.map-layers-btn:hover {
  background: #00ffcc;
  color: black;
}

/* ============================= */
/* Bulletin Scrolling Section */
/* ============================= */
.bulletin {
  width: 80%;
  max-width: 1000px;
  height: 400px;
  margin: 30px auto;
  background: rgba(0, 0, 0, 0.6);
  overflow-y: scroll;
  padding: 20px;
  border-radius: 20px;
  font-size: 1.2rem;
  line-height: 1.6;
  color: #00ffcc;
  box-shadow: inset 0 0 20px #ff33cc;
}

/* ============================= */
/* Donation + Proton Buttons */
/* ============================= */
.button-row {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 30px 0;
}

.neon-button {
  padding: 15px 25px;
  border-radius: 40px;
  background: #111;
  color: #00ffcc;
  border: 2px solid #00ffcc;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.neon-button:hover {
  background: #00ffcc;
  color: black;
  box-shadow: 0 0 20px #00ffcc, 0 0 40px #ff33cc;
}
