:root {
  --background-color: #1a1a2e;
  --primary-color: #ff0077;
  --secondary-color: #6a05ad;
  --panel-background: rgba(48, 57, 82, 0.7);
  --border-color: #5a5a70;
  --font-color: #c9c9d4;
  --header-font: 'Arial', sans-serif;
  --body-font: 'Courier New', monospace;
}

body {
  background-color: var(--background-color);
  color: var(--font-color);
  font-family: var(--body-font);
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.ghostnet-board-container {
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 2fr 1fr;
  gap: 20px;
  height: 100vh;
  padding: 20px;
  box-sizing: border-box;
}

/* This is a general style for the panels within the grid */
.panel {
  background-color: var(--panel-background);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  padding: 20px;
  overflow: hidden;
}

/* Specific styling for the ReactFlow component area */
.reactflow-wrapper {
  grid-column: 1 / span 1;
  grid-row: 1 / 2;
  border: var(--border-color) solid 1px;
  border-radius: 8px;
}

.react-flow__background {
  opacity: 0.1;
}

/* Specific styling for the Node Design Lab panel */
.node-design-lab {
  grid-column: 2 / 3;
  grid-row: 1 / 2;
}

/* Specific styling for the bottom row (Security Graph & Payload Sandbox) */
.bottom-panels-container {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

/* Specific styling for Security Graph */
.security-graph {
  background-color: var(--panel-background);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 10px;
}

/* Specific styling for Payload Sandbox */
.payload-sandbox {
  background-color: var(--panel-background);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 10px;
}

/* You may need to add more specific styles for the components themselves */
/* (e.g., button colors, input styles, etc.) */


