:root {
  --background-color: #1a001a;
  --panel-bg: rgba(20, 0, 20, 0.7);
  --font-color: #e0e0e0;
  --header-font: 'Orbitron', sans-serif;
  --body-font: 'Share Tech Mono', monospace;
  --neon-purple: #ae00ff;
  --neon-pink: #ff0077;
  --neon-blue: #00e0ff;
  --neon-green: #00ffaa;
  --border-glow-purple: 0 0 10px var(--neon-purple), 0 0 20px var(--neon-purple);
  --border-glow-pink: 0 0 10px var(--neon-pink), 0 0 20px var(--neon-pink);
}

.login-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background-color);
  font-family: var(--body-font);
  color: var(--font-color);
  background-image: 
    radial-gradient(at 20% 80%, rgba(174, 0, 255, 0.2), transparent),
    radial-gradient(at 80% 20%, rgba(255, 0, 119, 0.2), transparent);
  background-attachment: fixed;
}

.login-panel {
  background-color: var(--panel-bg);
  border: 1px solid var(--neon-purple);
  box-shadow: var(--border-glow-purple);
  border-radius: 10px;
  padding: 40px;
  text-align: center;
  backdrop-filter: blur(5px);
}

.login-title {
  font-family: var(--header-font);
  color: var(--neon-blue);
  text-shadow: 0 0 8px var(--neon-blue);
  margin-bottom: 10px;
  font-size: 2rem;
}

.login-subtitle {
  font-family: var(--body-font);
  color: var(--neon-green);
  text-shadow: 0 0 5px var(--neon-green);
  margin-bottom: 30px;
  font-size: 1.1rem;
}

.login-form {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

.login-input {
  background-color: rgba(0, 0, 0, 0.4);
  border: 1px solid var(--neon-blue);
  color: var(--font-color);
  padding: 15px;
  font-family: var(--body-font);
  font-size: 1rem;
  box-shadow: inset 0 0 5px rgba(0, 224, 255, 0.3);
}

.login-input::placeholder {
  color: #888;
}

.login-input:focus {
  outline: none;
  border-color: var(--neon-pink);
  box-shadow: inset 0 0 8px rgba(255, 0, 119, 0.5), 0 0 5px var(--neon-pink);
}

.login-btn {
  padding: 15px 25px;
  font-size: 1.2rem;
  font-family: var(--header-font);
  background-color: var(--neon-pink);
  color: var(--background-color);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 0 0 10px var(--neon-pink);
  transition: all 0.2s ease-in-out;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.login-btn:hover {
  background-color: var(--neon-purple);
  box-shadow: 0 0 15px var(--neon-purple), 0 0 25px var(--neon-purple);
  transform: translateY(-2px);
}

.login-error {
  margin-top: 20px;
  color: #ff0000;
  font-size: 1rem;
  text-shadow: 0 0 5px #ff0000;
  animation: pulse-red 1s infinite alternate;
}

@keyframes pulse-red {
  from { text-shadow: 0 0 5px #ff0000; }
  to { text-shadow: 0 0 15px #ff0000; }
}

