:root {
  --active-color: #00ffaa; /* Green for active */
  --standby-color: #ffcc00; /* Yellow for standby */
  --offline-color: #ff0077; /* Pink for offline */
  --gunmetal-gray: #303952;
  --gunmetal-dark: #202633;
  --font-color: #c9c9d4;
  --panel-border: 1px solid #5a5a70;
}

.ghost-reactor {
  background-color: var(--gunmetal-gray);
  border-radius: 8px;
  border: var(--panel-border);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  padding: 20px;
  color: var(--font-color);
  font-family: 'Roboto Mono', monospace;
  text-align: center;
}

h3 {
  color: #fff;
  text-shadow: 0 0 5px rgba(255, 255, 255, 0.5);
}

.status-indicator {
  padding: 10px 20px;
  border-radius: 20px;
  font-weight: bold;
  margin-bottom: 20px;
  transition: all 0.3s ease;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}

.status-indicator.active {
  background-color: var(--active-color);
  color: var(--gunmetal-dark);
  box-shadow: 0 0 15px var(--active-color);
}

.status-indicator.standby {
  background-color: var(--standby-color);
  color: var(--gunmetal-dark);
  box-shadow: 0 0 15px var(--standby-color);
}

.status-indicator.offline {
  background-color: var(--offline-color);
  color: #fff;
  box-shadow: 0 0 15px var(--offline-color);
}

.control-buttons button {
  background-color: var(--gunmetal-dark);
  color: var(--font-color);
  border: 1px solid var(--font-color);
  padding: 10px 20px;
  margin: 0 5px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.control-buttons button:hover:not(:disabled) {
  background-color: var(--font-color);
  color: var(--gunmetal-dark);
}

.control-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

